import { SvgIcon } from './SvgIcon.js';

const STATUS = {
  idle: 'idle',
  pending: 'pending',
  resolved: 'resolved',
  rejected: 'rejected',
  disabled: 'disabled',
};

const BUTTON_NAME = {
  upArrow: 'up-arrow',
  spinner: 'spinner',
  checkMark: 'check-mark',
  cross: 'cross',
  notAllowed: 'not-allowed',
};

export function UploadButton({
  type = 'button',
  status = 'idle',
  label,
  size,
  icon,
  disabled,
  children,
  ...restProps
}) {
  let buttonLabel = label ?? getButtonLabel(status);
  let iconName = icon ?? getIconName(status);
  let isDisabled = disabled ?? status.includes('disabled');

  if (isDisabled) iconName = BUTTON_NAME.notAllowed;

  return (
    <button
      type={type}
      className="UploadButton"
      disabled={isDisabled}
      {...restProps}
    >
      {children ?? buttonLabel}
      {status !== STATUS.pending ? (
        <SvgIcon type={iconName} size={size} />
      ) : (
        <SpinnerAnimate size={size} />
      )}
    </button>
  );
}

function SpinnerAnimate({ size = 12, color = 'currentColor' }) {
  return (
    <svg
      width={size}
      height={size}
      stroke={color}
      viewBox={`0 0 ${size} ${size}`}
    >
      <g className="spinner__group">
        <circle
          cx={size / 2}
          cy={size / 2}
          r={size / 2 - 1}
          fill="none"
          strokeWidth="1.5"
        />
      </g>
    </svg>
  );
}

function getButtonLabel(status) {
  switch (status) {
    case STATUS.idle:
    case STATUS.disabled:
      return '업로드';
    case STATUS.pending:
      return '업로드 중';
    case STATUS.resolved:
      return '완료';
    case STATUS.rejected:
      return '실패';
    default:
      return false;
  }
}

function getIconName(status) {
  switch (status) {
    case STATUS.idle:
      return BUTTON_NAME.upArrow;
    case STATUS.pending:
      return BUTTON_NAME.spinner;
    case STATUS.resolved:
      return BUTTON_NAME.checkMark;
    case STATUS.rejected:
      return BUTTON_NAME.cross;
    case STATUS.disabled:
      return BUTTON_NAME.notAllowed;
  }
}
